@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.4rem;
}

h3 {
  font-size: 2rem;
}

.app {
  width: 100%;
  /* display: flex; */
  margin: 0 auto;
  padding: 100px 0;
  height: 100%;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  /* color: #fff; */
}

.app:before {
  content: '';
  background-image: url(../src/images/back.png);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app .search {
  text-align: center;
  padding: 1rem;
  width: 50%;
  margin: 0 auto;
}
.location{
  width: 50%;
  margin: 0 auto;
}
.temp{
  width: 50%;
  font-size: 35px;
  margin: 0 auto;
  /* padding: 10px; */
}
.search-icon{
  float: right;
  position: relative;
  top: -25px;
}
.sunset-text, .sunrise-text{
  position: relative;
  top: 10px;
}
.sunset, .sunrise{
  display: flex;
  font-size: 1.5rem;
  /* padding: 20px; */
}
.sunset-logo,.sunrise-logo{
  /* font-size: 2rem; */
  font-size: 50px;
  padding:5px;
  
  color: #ffd700;
}

input[type='text']{
  font-size: 20px;
    display: block;
    width: 100%;
    border: 0;
    /* margin: 0 auto; */
    padding: 10px 5px;
    background: transparent no-repeat;
    background-image: linear-gradient(to bottom, #ffd700, #ffd700), linear-gradient(to bottom, rgb(109, 104, 104), silver);
    /* sizes for the 2 images (default state) */
    background-size: 0 2px, 100% 1px;
    /* positions for the 2 images. Change both "50%" to "0%" or "100%" and tri again */
    background-position: 100% 100%, 50% 100%;
    transition: background-size 1s cubic-bezier(0.64, 0.09, 0.08, 1);
    color: #fff;

}
input:focus{
    background-size: 100% 2px, 100% 1px;
    outline: none;
}

.container {
  max-width: 100%;
  height: 500px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 26%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  font: normal 36px 'Cookie', cursive;
  height: 400px;
  margin: 0 auto;
  /* display: flex; */
    /* justify-content: space-evenly; */
    text-align: center;
    width: 50%;
    /* margin: 0.5rem auto; */
    padding: 1rem;
    /* width: 80%; */
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.253);
  
}
.description img{
  width: 100px;
  height: 100px;
  Top: 97px;
  position: relative;
  left: -104px;
} 

.app .description {
  position: relative;
  right: -60%;
  top: -46%;
  font: Caladea;
  transform-origin: 0 0;
  /* transform: rotate(269deg); */
}


.app .bottom {
  position: relative;
  top: 4rem;
  /* bottom: 50px; */
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100vw;
  margin: 1rem auto;
  padding: 1rem;
}
.app .bottom p{
  padding-top: 10px;
}

.bold {
  font-weight: 700;
}
@media screen and (min-width: 800px) {


  .app {
    width: 100%;
    /* margin: 0 auto; */
    height: 100vh;
    position: relative;
    background-color: rgba(0,0,0,0.4);
    color: #fff;
  }
  /* .feels, .humidity,.wind{
    font: 20px;
    
  } */
  .app .description {
    position: absolute;
    right: 26.5%;
    top: 9%;
    font: normal 36px 'Cookie', cursive;
    width: fit-content;
    text-align: justify;
    height: 100px;
  }
  .app .bottom {
    font: normal 36px 'Cookie', cursive;
    width: 50%;
    text-align: center;
    margin: 0 auto;
    position: relative;
    top: 56px;
}
}
@media screen and (min-width: 300px) {


  .app {
    width: 100%;
    /* margin: 0 auto; */
    height: 100vh;
    position: relative;
    background-color: rgba(0,0,0,0.4);
    color: #fff;
  }
  .feels, .humidity,.wind{
    font: 20px;
  }
}